import React, { Component, Fragment } from "react";
import { S, wait, ProgressBar } from "./alabsi";
import { userData, addedByUsers, data } from "./Data";
import { dateGetter, dateIndex } from "./MainPage";
import { updateRenderState } from ".";
import CreateFood from "./CreateFood";

function SelectMenu(props) {
  return (
    <Fragment>
      <option className="itemViewerSelectItems" value={props.servingValue}>
        {props.servingName}
      </option>
    </Fragment>
  );
}

export default class ItemViewer extends Component {
  state = {
    selectValue: this.props.data.serving ? this.props.data.serving[0][1] : 1,
    inputValue: this.props.data.serving ? 1 : 100,
  };

  componentDidMount() {
    this.introPageTransition();
    this.addProgressBars();
    this.chechFavorite();

    if (window.location.pathname.includes("diary")) {
      this.setState({ selectValue: this.props.value, inputValue: this.props.input });
      document.getElementById("itemViewerSelectMeal").value = this.props.mealName;
    } else if (window.location.pathname === "/CreateNewMeal") {
      this.setState({ selectValue: this.props.value, inputValue: this.props.input });
    } else if (window.location.pathname === "/EditMeal") {
      this.setState({ selectValue: this.props.value, inputValue: this.props.input });
    } else if (window.location.pathname.includes("recentlyAdded")) {
      this.setState({ selectValue: this.props.value, inputValue: this.props.input });
    }
  }

  editHandle = () => {
    updateRenderState(<CreateFood key={Math.random() * 100} id={this.props.data.id} />);
		window.history.replaceState("", "", window.location.pathname + "/EditFood");
  }

  trashCanHandle = () => {
    const [id, value, input, mealName, path, hash] = [
      this.props.data.id,
      this.props.value,
      this.props.input,
      this.props.mealName,
      window.location.pathname,
      window.location.hash,
    ];

    // delete items from meals
    if (path === "/CreateNewMeal" || path === "/EditMeal") {
      const arrayIndex = userData.favorite.tmpMeal.mealContent.findIndex(
        (e) => e[0] === id && e[1] === value && e[2] === input
      );
      userData.favorite.tmpMeal.mealContent.splice(arrayIndex, 1);

      // delete addedByUser itmes
    } else if (hash.includes("#selectMeal#addedByUsers")) {
      const arrayIndexInAdded = addedByUsers.findIndex((e) => e.id === id);
      addedByUsers.splice(arrayIndexInAdded, 1);
      const arrayIndexInData = data.findIndex((e) => e.id === id);
      data.splice(arrayIndexInData, 1);
      userData.favorite.food.splice(userData.favorite.food.indexOf(id), 1);
      window.localStorage.setItem("addedByUsers", JSON.stringify(addedByUsers));

      // delete all addedByUser items from diary
      for (let index = 0; index < userData.diary.length; index++) {
        const element = userData.diary[index];
        for (const key in element) {
          if (key !== "date") {
            const ob = element[key];
            for (let i = ob.length - 1; i >= 0; i--) {
              const el = ob[i];
              if (el[0] === id) ob.splice(i, 1);
            }
            if (ob.length === 0) delete element[key];
          }
        }
      }

      // delete all addedByUser items from meals
      if (userData.favorite.meals.length > 0) {
        for (let index = 0; index < userData.favorite.meals.length; index++) {
          const element = userData.favorite.meals[index];
          for (let i = element.content.length - 1; i >= 0; i--) {
            const el = element.content[i];
            if (el[0] === id) element.content.splice(i, 1);
          }
          if (element.content.length === 0) userData.favorite.meals.splice(index, 1);
        }
      }

      // delete items from diary
    } else {
      const getDataByDate = userData.diary.filter((e) => e.date === dateGetter(dateIndex))[0];
      const objectIndex = userData.diary.indexOf(getDataByDate);
      const arrayIndex = userData.diary[objectIndex][mealName].findIndex(
        (e) => e[0] === id && e[1] === value && e[2] === input
      );
      userData.diary[objectIndex][mealName].splice(arrayIndex, 1);
      if (userData.diary[objectIndex][mealName].length === 0) delete userData.diary[objectIndex][mealName];
    }
    window.history.back();
  };

  okButtonHandle = () => {
    const [id, value, input, path] = [this.props.data.id, this.props.value, this.props.input, window.location.pathname];

    const getDataByDate = userData.diary.filter((e) => e.date === dateGetter(dateIndex))[0];

    const mealName =
      window.location.hash.split("#")[1] === "selectMeal"
        ? document.getElementById("itemViewerSelectMeal").value
        : window.location.pathname.includes("diary")
        ? this.props.mealName
        : window.location.hash.split("#")[1];

    const itemArray = [id, Number(this.state.selectValue), Number(this.state.inputValue)];

    if (path.includes("diary")) {
      const newMealName = document.getElementById("itemViewerSelectMeal").value;
      const objectIndex = userData.diary.indexOf(getDataByDate);
      const arrayIndex = userData.diary[objectIndex][mealName].findIndex(
        (e) => e[0] === id && e[1] === value && e[2] === input
      );
      if (mealName !== newMealName) {
        userData.diary[objectIndex][mealName].splice(arrayIndex, 1);
        if (userData.diary[objectIndex][mealName].length === 0) delete userData.diary[objectIndex][mealName];

        userData.diary[objectIndex][newMealName]
          ? userData.diary[objectIndex][newMealName].push(itemArray)
          : (userData.diary[objectIndex][newMealName] = [itemArray]);
      } else {
        userData.diary[objectIndex][mealName].splice(arrayIndex, 1, itemArray);
      }
    } else if (
      path === "/CreateNewMeal/search" ||
      path === "/CreateNewMeal/favorites" ||
      path === "/CreateNewMeal/recentlyAdded" ||
      path === "/EditMeal/search" ||
      path === "/EditMeal/favorites" ||
      path === "/EditMeal/recentlyAdded"
    ) {
      userData.favorite.tmpMeal.mealContent.push(itemArray);
    } else if (path === "/CreateNewMeal" || path === "/EditMeal") {
      const arrayIndex = userData.favorite.tmpMeal.mealContent.findIndex(
        (e) => e[0] === id && e[1] === value && e[2] === input
      );
      userData.favorite.tmpMeal.mealContent.splice(arrayIndex, 1, itemArray);
    } else {
      if (getDataByDate) {
        getDataByDate[mealName] ? getDataByDate[mealName].push(itemArray) : (getDataByDate[mealName] = [itemArray]);
      } else {
        userData.diary.push({ date: dateGetter(dateIndex), [mealName]: [itemArray] });
      }
    }

    window.history.back();
  };

  calcNutrition = (i) => ((this.state.inputValue * this.state.selectValue) / 100) * i;

  renderSelectMenu = () => {
    if (this.props.data.serving) {
      return this.props.data.serving.map((e) => (
        <SelectMenu key={Math.random() * 1000} servingName={e[0]} servingValue={e[1]} />
      ));
    }
  };

  chechFavorite = () => {
    if (userData.favorite.food.includes(this.props.data.id)) {
      S("#itemViewerFavouriteButtonFill").css("fill", "white");
    }
  };

  addOrRemoveFavorite = () => {
    if (userData.favorite.food.includes(this.props.data.id)) {
      userData.favorite.food.splice(userData.favorite.food.indexOf(this.props.data.id), 1);
      S("#itemViewerFavouriteButtonFill").css("fill", "none");
      this.showToast("تمت الإزالة من المفضلة");
    } else {
      userData.favorite.food.unshift(this.props.data.id);
      S("#itemViewerFavouriteButtonFill").css("fill", "white");
      this.showToast("تمت الإضافة إلى المفضلة");
    }
  };

  showToast = async (text) => {
    S("#itemViewerToast").text(text).css("display", "block");
    await wait(20);
    S("#itemViewerToast").css("opacity", 1).css("bottom", "75px");
    await wait(1500);
    S("#itemViewerToast").css("opacity", 0).css("bottom", "0px");
    await wait(350);
    S("#itemViewerToast").removeAttr("style");
  };

  calcFatPercent = () =>
    Math.min(Math.max(Math.round(((this.props.data.fat * 9) / this.props.data.kcal) * 100), 0), 100);
  calcProteinPercent = () =>
    Math.min(Math.max(Math.round(((this.props.data.protein * 4) / this.props.data.kcal) * 100), 0), 100);
  CarbPercent = () => Math.min(Math.max(Math.round(((this.props.data.carb * 4) / this.props.data.kcal) * 100), 0), 100);
  calcCarbPercent = () =>
    this.CarbPercent() > 15
      ? Math.min(Math.max(100 - (this.calcFatPercent() + this.calcProteinPercent()), 0), 100)
      : this.CarbPercent();

  addProgressBars = () => {
    ProgressBar("#nutritionInfoCrabsPercent", {
      input: this.calcCarbPercent(),
      R: 40,
      width: 5,
      fillColor: "none",
      progressColor: "#2b2b2b;",
      textColor: "#2b2b2b",
      backgroundColor: "#F0F0F0",
      lineCap: "none",
    });
    ProgressBar("#nutritionInfoProteinPercent", {
      input: this.calcProteinPercent(),
      R: 40,
      width: 5,
      fillColor: "none",
      progressColor: "#2b2b2b",
      textColor: "#2b2b2b",
      backgroundColor: "#F0F0F0",
      lineCap: "none",
    });
    ProgressBar("#nutritionInfoFatPercent", {
      input: this.calcFatPercent(),
      R: 40,
      width: 5,
      fillColor: "none",
      progressColor: "#2b2b2b",
      textColor: "#2b2b2b",
      backgroundColor: "#F0F0F0",
      lineCap: "none",
    });
    const carbsTitle = document.createElement("p");
    const proteinTitle = document.createElement("p");
    const fatTitle = document.createElement("p");
    carbsTitle.classList.add("percentTitles");
    proteinTitle.classList.add("percentTitles");
    fatTitle.classList.add("percentTitles");
    carbsTitle.innerHTML = "الكربوهيدرات";
    proteinTitle.innerHTML = "البروتين";
    fatTitle.innerHTML = "الدهون";
    document.getElementById("nutritionInfoCrabsPercent").append(carbsTitle);
    document.getElementById("nutritionInfoProteinPercent").append(proteinTitle);
    document.getElementById("nutritionInfoFatPercent").append(fatTitle);
  };

  introPageTransition = async () => {
    S("#itemViewerPage")
      .css("position", "fixed")
      .css("bottom", "-100vh")
      .css("left", "50%")
      .css("opacity", "0")
      .css("transform", "translate(-50%, 100%)");
    S("#itemViewerHeader").css("position", "relative");
    await wait(10);
    S("#itemViewerPage").css("bottom", "100%").css("opacity", "1");
    await wait(S("#itemViewerPage").getStyle("transition-duration", true) * 1000);
    S("#itemViewerPage").removeAttr("style");
    S("#itemViewerHeader").removeAttr("style");
  };

  backButtonHandle = () => window.history.back();

  checkHash = () => {
    let hash;
    switch (window.location.hash.split("#")[1]) {
      case "breakfast":
        hash = "الفطور";
        break;
      case "lunch":
        hash = "الغداء";
        break;
      case "dinner":
        hash = "العشاء";
        break;
      case "snacks":
        hash = "وجبة خفيفة";
        break;
      default:
        break;
    }
    return hash;
  };

  render() {
    return (
      <div id="itemViewerPage">
        <p id="itemViewerToast"></p>
        <header id="itemViewerHeader">
          <svg
            id="itemViewerBackButton"
            onClick={this.backButtonHandle}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path d="M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z" />
          </svg>
          <p id="itemViewerHeaderTitle">
            {this.checkHash()}
            <svg
              id="itemViewerTrashCan"
              style={{
                display:
                  window.location.pathname.includes("diary") ||
                  window.location.pathname === "/CreateNewMeal" ||
                  window.location.pathname === "/EditMeal" ||
                  window.location.hash.includes("#selectMeal#addedByUsers")
                    ? "block"
                    : "none",
              }}
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
              onClick={this.trashCanHandle}
            >
              <path d="M3 6v18h18v-18h-18zm5 14c0 .552-.448 1-1 1s-1-.448-1-1v-10c0-.552.448-1 1-1s1 .448 1 1v10zm5 0c0 .552-.448 1-1 1s-1-.448-1-1v-10c0-.552.448-1 1-1s1 .448 1 1v10zm5 0c0 .552-.448 1-1 1s-1-.448-1-1v-10c0-.552.448-1 1-1s1 .448 1 1v10zm4-18v2h-20v-2h5.711c.9 0 1.631-1.099 1.631-2h5.315c0 .901.73 2 1.631 2h5.712z" />
            </svg>
          </p>
          <svg
            id="itemViewerFavouriteButton"
            onClick={this.addOrRemoveFavorite}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
            style={{
              display: !window.location.hash.includes("addedByUsers") ? "block" : "none",
            }}
          >
            <path
              id="itemViewerFavouriteButtonFill"
              fill="none"
              d="M12 4.248c-3.148-5.402-12-3.825-12 2.944 0 4.661 5.571 9.427 12 15.808 6.43-6.381 12-11.147 12-15.808 0-6.792-8.875-8.306-12-2.944z"
            />
            <path d="M6.28 3c3.236.001 4.973 3.491 5.72 5.031.75-1.547 2.469-5.021 5.726-5.021 2.058 0 4.274 1.309 4.274 4.182 0 3.442-4.744 7.851-10 13-5.258-5.151-10-9.559-10-13 0-2.676 1.965-4.193 4.28-4.192zm.001-2c-3.183 0-6.281 2.187-6.281 6.192 0 4.661 5.57 9.427 12 15.808 6.43-6.381 12-11.147 12-15.808 0-4.011-3.097-6.182-6.274-6.182-2.204 0-4.446 1.042-5.726 3.238-1.285-2.206-3.522-3.248-5.719-3.248z" />
          </svg>
          <svg
            id="itemViewerEdit"
            onClick={this.editHandle}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
            style={{
              display: window.location.hash.includes("addedByUsers") ? "block" : "none",
            }}
          >
            <path d="M7.127 22.562l-7.127 1.438 1.438-7.128 5.689 5.69zm1.414-1.414l11.228-11.225-5.69-5.692-11.227 11.227 5.689 5.69zm9.768-21.148l-2.816 2.817 5.691 5.691 2.816-2.819-5.691-5.689z" />
          </svg>
        </header>
        <div id="itemViewerHeaderButtonsContainer">
          <p id="itemViewerHeaderItemName">{this.props.data.name}</p>
          <div id="itemViewerOkButtonBg" onClick={this.okButtonHandle}>
            <svg id="itemViewerOkButton" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
              <path d="M21 6.285l-11.16 12.733-6.84-6.018 1.319-1.49 5.341 4.686 9.865-11.196 1.475 1.285z" />
            </svg>
          </div>
        </div>
        <div id="itemViewerInputsContainer">
          <input
            id="itemViewerInput"
            type="number"
            value={this.state.inputValue}
            onChange={(e) => this.setState({ inputValue: e.target.value < 0 ? 0 : Number(e.target.value) })}
            onClick={(e) => e.target.select()}
          />

          <select
            id="itemViewerSelectServing"
            value={this.state.selectValue}
            onChange={(e) => this.setState({ selectValue: Number(e.target.value) })}
          >
            {this.renderSelectMenu()}
            <option className="itemViewerSelectServingItems" value="1">
              غرام
            </option>
          </select>
        </div>
        <div style={{ backgroundColor: "white" }}>
          <select
            id="itemViewerSelectMeal"
            style={{
              display:
                window.location.hash.includes("selectMeal") || window.location.pathname.includes("diary")
                  ? "block"
                  : "none",
            }}
          >
            <option className="itemViewerSelectMealItems" value="breakfast">
              الفطور
            </option>
            <option className="itemViewerSelectMealItems" value="lunch">
              الغداء
            </option>
            <option className="itemViewerSelectMealItems" value="dinner">
              العشاء
            </option>
            <option className="itemViewerSelectMealItems" value="snacks">
              وجبة خفيفة
            </option>
          </select>
          <p id="itemViewerKcal">
            {Math.round(this.calcNutrition(this.props.data.kcal))}
            <span>سعرة حرارية</span>
          </p>
        </div>
        <div id="itemViewerNutritionInfoContainer">
          <p id="itemViewerNutritionInfoTitle">المعلومات الغذائية</p>
          <p
            id="itemViewerNutritionInfoSubTitle"
            style={{ display: !this.props.data.id.includes("addedByUsers") ? "block" : "none" }}
          >
            تم توثيقه
            <svg className="searchItemsVerified" viewBox="0 0 128 128" xmlns="http://www.w3.org/2000/svg">
              <g>
                <circle cx="64" cy="64" fill="#4ce797" r="43.125" />
                <circle cx="64" cy="64" fill="#e7f8fc" r="34.544" />
                <path
                  d="m58.211 81.479a3.894 3.894 0 0 1 -2.694-1.079l-11.569-11.1a3.892 3.892 0 1 1 5.388-5.618l8.59 8.239 20.468-24.03a3.893 3.893 0 1 1 5.927 5.048l-23.147 27.171a3.893 3.893 0 0 1 -2.767 1.364c-.065.003-.13.005-.196.005z"
                  fill="#4ce797"
                />
              </g>
            </svg>
          </p>
          <div id="nutritionInfoPercentContainer">
            <div id="nutritionInfoCrabsPercent"></div>
            <div id="nutritionInfoProteinPercent"></div>
            <div id="nutritionInfoFatPercent"></div>
          </div>
          <div id="itemViewerNutritionInfoDetail">
            <div id="itemViewerNutritionInfoTitles">
              <p className="itemViewerNutritionInfoMain">البروتين</p>
              <p className="itemViewerNutritionInfoMain">الكربوهيدرات</p>
              <p className="itemViewerNutritionInfoSub">الألياف</p>
              <p className="itemViewerNutritionInfoSub">السكر</p>
              <p className="itemViewerNutritionInfoMain">الدهون</p>
              <p className="itemViewerNutritionInfoSub">الدهون المشبعة</p>
              <p className="itemViewerNutritionInfoSub">الدهون الغير مشبعة</p>
              <p className="itemViewerNutritionInfoMain">آخر</p>
              <p className="itemViewerNutritionInfoSub">الكوليسترول</p>
              <p className="itemViewerNutritionInfoSub">الصوديوم</p>
              <p className="itemViewerNutritionInfoSub">البوتاسيوم</p>
            </div>
            <div id="itemViewerNutritionInfoValues">
              <p className="itemViewerNutritionInfoMain">
                {Number(this.calcNutrition(this.props.data.protein).toFixed(1))} غ
              </p>
              <p className="itemViewerNutritionInfoMain">
                {Number(this.calcNutrition(this.props.data.carb).toFixed(1))} غ
              </p>
              <p className="itemViewerNutritionInfoSub">
                {Number(this.calcNutrition(this.props.data.fiber).toFixed(1))} غ
              </p>
              <p className="itemViewerNutritionInfoSub">
                {Number(this.calcNutrition(this.props.data.sugar).toFixed(1))} غ
              </p>
              <p className="itemViewerNutritionInfoMain">
                {Number(this.calcNutrition(this.props.data.fat).toFixed(1))} غ
              </p>
              <p className="itemViewerNutritionInfoSub">
                {Number(this.calcNutrition(this.props.data.saturatedFat).toFixed(2))} غ
              </p>
              <p className="itemViewerNutritionInfoSub">
                {Number(this.calcNutrition(this.props.data.unsaturatedFat).toFixed(2))} غ
              </p>
              <p className="itemViewerNutritionInfoMain" style={{ visibility: "hidden" }}>
                ......
              </p>
              <p className="itemViewerNutritionInfoSub">
                {this.calcNutrition(this.props.data.cholesterol).toFixed(0)} ملغ
              </p>
              <p className="itemViewerNutritionInfoSub">{this.calcNutrition(this.props.data.sodium).toFixed(0)} ملغ</p>
              <p className="itemViewerNutritionInfoSub">
                {this.calcNutrition(this.props.data.potassium).toFixed(0)} ملغ
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
